import React, { useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Button,
  Card,
  Checkbox,
  Divider,
  IconButton,
  InputAdornment,
  Link,
  SvgIcon,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Tabs,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import {
  Edit as EditIcon,
  Search as SearchIcon
} from 'react-feather';

const tabs = [
  {
    value: 'all',
    label: 'Tất cả'
  }
];

const sortOptions = [
  {
    value: 'name|asc',
    label: 'Tên lớp học (tăng)'
  },
  {
    value: 'name|desc',
    label: 'Tên lớp học (giảm)'
  },
  {
    value: 'years|asc',
    label: 'Niên khóa (tăng)'
  },
  {
    value: 'years|desc',
    label: 'Niên khóa (giảm)'
  }
];

const applyFilters = (sclasses, query, filters) => {
  return sclasses.filter((sclass) => {
    let matches = true;

    if (query) {
      const properties = ['id', 'name'];
      let containsQuery = false;

      properties.forEach((property) => {
        if (sclass[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    Object.keys(filters).forEach((key) => {
      const value = filters[key];

      if (value && sclass[key] !== value) {
        matches = false;
      }
    });

    return matches;
  });
};

const applyPagination = (sclasses, page, limit) => {
  return sclasses.slice(page * limit, page * limit + limit);
};

const descendingComparator = (a, b, orderBy) => {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }

  if (b[orderBy] > a[orderBy]) {
    return 1;
  }

  return 0;
};

const getComparator = (order, orderBy) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};

const applySort = (sclasses, sort) => {
  const [orderBy, order] = sort.split('|');
  const comparator = getComparator(order, orderBy);
  const stabilizedThis = sclasses.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);

    if (order !== 0) return order;

    return a[1] - b[1];
  });

  return stabilizedThis.map((el) => el[0]);
};

const useStyles = makeStyles((theme) => ({
  root: {},
  queryField: {
    width: 500
  },
  bulkOperations: {
    position: 'relative'
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: 'absolute',
    width: '100%',
    zIndex: 2,
    backgroundColor: theme.palette.background.default
  },
  bulkAction: {
    marginLeft: theme.spacing(2)
  },
  avatar: {
    height: 42,
    width: 42,
    marginRight: theme.spacing(1)
  }
}));

const Results = ({
  sclasses,
  ...rest
}) => {


  const classes = useStyles();
  const [currentTab, setCurrentTab] = useState('all');
  const [selectedClasses, setSelectedClasses] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState('');
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    hasAcceptedMarketing: null,
    isProspect: null,
    isReturning: null
  });

  const handleTabsChange = (event, value) => {
    const updatedFilters = {
      ...filters,
      hasAcceptedMarketing: null,
      isProspect: null,
      isReturning: null
    };

    if (value !== 'all') {
      updatedFilters[value] = true;
    }

    setFilters(updatedFilters);
    setSelectedClasses([]);
    setCurrentTab(value);
  };

  const handleQueryChange = (event) => {
    event.persist();
    setQuery(event.target.value);
  };

  const handleSortChange = (event) => {
    event.persist();
    setSort(event.target.value);
  };

  const handleSelectAllClasses = (event) => {
    setSelectedClasses(event.target.checked
      ? sclasses.map((sclass) => sclass.id)
      : []);
  };

  const handleSelectOneSchool = (event, sclassId) => {
    if (!selectedClasses.includes(sclassId)) {
      setSelectedClasses((prevSelected) => [...prevSelected, sclassId]);
    } else {
      setSelectedClasses((prevSelected) => prevSelected.filter((id) => id !== sclassId));
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value));
  };

  const filteredClasses = applyFilters(sclasses, query, filters);
  const sortedClasses = applySort(filteredClasses, sort);
  const paginatedClasses = applyPagination(sortedClasses, page, limit);
  const enableBulkOperations = selectedClasses.length > 0;
  const selectedSomeClasses = selectedClasses.length > 0 && selectedClasses.length < sclasses.length;
  const selectedAllClasses = selectedClasses.length === sclasses.length;

  return (
    <Card
      className={clsx(classes.root)}
      {...rest}
    >
      <Tabs
        onChange={handleTabsChange}
        scrollButtons="auto"
        textColor="secondary"
        value={currentTab}
        variant="scrollable"
      >
        {tabs.map((tab) => (
          <Tab
            key={tab.value}
            value={tab.value}
            label={tab.label}
          />
        ))}
      </Tabs>
      <Divider />
      <Box
        p={2}
        minHeight={56}
        display="flex"
        alignItems="center"
      >
        <TextField
          className={classes.queryField}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SvgIcon
                  fontSize="small"
                  color="action"
                >
                  <SearchIcon />
                </SvgIcon>
              </InputAdornment>
            )
          }}
          onChange={handleQueryChange}
          placeholder="Tìm kiếm theo tên lớp"
          value={query}
          variant="outlined"
        />
        <Box flexGrow={1} />
        <TextField
          label="Sắp xếp"
          name="sort"
          onChange={handleSortChange}
          select
          SelectProps={{ native: true }}
          value={sort}
          variant="outlined"
        >
          {sortOptions.map((option) => (
            <option
              key={option.value}
              value={option.value}
            >
              {option.label}
            </option>
          ))}
        </TextField>
      </Box>
      {enableBulkOperations && (
        <div className={classes.bulkOperations}>
          <div className={classes.bulkActions}>
            <Checkbox
              checked={selectedAllClasses}
              indeterminate={selectedSomeClasses}
              onChange={handleSelectAllClasses}
            />
            <Button
              variant="outlined"
              className={classes.bulkAction}
            >
              Mua khóa học
            </Button>
          </div>
        </div>
      )}
      <PerfectScrollbar>
        <Box minWidth={700}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox
                    checked={selectedAllClasses}
                    indeterminate={selectedSomeClasses}
                    onChange={handleSelectAllClasses}
                  />
                </TableCell>
                <TableCell>
                  
                  Tên lớp
                </TableCell>
                <TableCell>
                  Mã lớp
                </TableCell>
                <TableCell>
                  Niên khóa 
                </TableCell>
                <TableCell>
                  Giáo viên chủ nhiệm
                </TableCell>
                <TableCell>
                  Số học sinh 
                </TableCell>
                <TableCell align="right">
                  Actions
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedClasses.map((sclass) => {
                const isSchoolSelected = selectedClasses.includes(sclass.id);
                return (
                  <TableRow
                    hover
                    key={sclass.id}
                    selected={isSchoolSelected}
                  >
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={isSchoolSelected}
                        onChange={(event) => handleSelectOneSchool(event, sclass.id)}
                        value={isSchoolSelected}
                      />
                    </TableCell>
                    <TableCell>
                      <Box
                        display="flex"
                        alignItems="center"
                      >
                          <Link
                            color="inherit"
                            component={RouterLink}
                            to={"/school/management/classes/edit/"+ sclass.id}
                            variant="h6"
                          >
                          {sclass.name}
                          </Link>  
                          
                        
                      </Box>
                    </TableCell>
                  <TableCell>
                  {sclass.id}
                  </TableCell>
                    <TableCell>
                    {sclass.years}
                    </TableCell>
                    <TableCell>
                    {sclass.teacher}
                    </TableCell>
                    <TableCell>
                    {sclass.quantity}
                    </TableCell>
                    <TableCell align="right">
                      <IconButton
                        component={RouterLink}
                        to={"/school/management/classes/edit/"+ sclass.id}
                      >
                        <SvgIcon fontSize="small">
                          <EditIcon />
                        </SvgIcon>
                      </IconButton>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredClasses.length}
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  sclasses: PropTypes.array.isRequired
};

Results.defaultProps = {
  sclasses: []
};

export default Results;
